import { stopSubmit } from "redux-form";
import { authAPI, profileAPI } from "../API/api";

const SET_USER_DATA = "SET-USER-DATA";
const SET_PROFILE_DATA = "SET-PROFILE-DATA";

let initialState = {
  userId: null,
  email: null,
  login: null,
  photos: null,
  isAuth: false,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.data,
        // isAuth: true,
      };

    case SET_PROFILE_DATA:
      return {
        ...state,
        ...action.photos,
      };

    default:
      return state;
  }
};

export const setUserDataActionCreator = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  data: { userId, email, login, isAuth },
});
export const setProfileDataActionCreator = (photos) => ({
  type: SET_PROFILE_DATA,
  photos,
});

export const authMeThunkCreator = () => {
  return async (dispatch) => {
    const response = await authAPI.getAuth();

    if (response.data.resultCode === 0) {
      let { id, email, login } = response.data.data;
      dispatch(setUserDataActionCreator(id, email, login, true));
      profileAPI.getProfile(id).then((response_1) => {
        dispatch(setProfileDataActionCreator(response_1.data.photos.large));
      });
    }
  };
};

export const loginMeThunkCreator = (email, password, rememberMe) => {
  return async (dispatch) => {
    const response = await authAPI.logIn(email, password, rememberMe);
    if (response.data.resultCode === 0) {
      dispatch(authMeThunkCreator());
    } else {
      let message =
        response.data.messages.length > 0
          ? response.data.messages[0]
          : "Какая-то ошибка";
      dispatch(stopSubmit("Login", { _error: message }));
    }
  };
};
export const logoutMeThunkCreator = () => {
  return async (dispatch) => {
    const response = await authAPI.logOut();
    if (response.data.resultCode === 0) {
      dispatch(setUserDataActionCreator(null, null, null, false));
    }
  };
};

export default authReducer;
