import "./Header.scss";
import { NavLink } from "react-router-dom";
import userAva from "../../assets/img/userDF.png";

const Header = (props) => {
  return (
    <header className="header">
      <div className="header__inner">
        <a href="/">
          <img
            className="header__logo"
            src="https://image.flaticon.com/icons/png/128/187/187902.png"
            alt=""
          />
        </a>
        <div className="header__login">
          {props.isAuth ? (
            props.login
          ) : (
            <NavLink to={"/login"}> Войдите</NavLink>
          )}
          <div className="header__login--ava">
            <img
            onDoubleClick={props.logOut}
              src={!props.userAva ? userAva : props.userAva}
              alt=""
              srcSet=""
            />
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
